@-webkit-keyframes moving_right_to_left {
  from {
    opacity: 0;
    -webkit-transform: translateX(200px);
            transform: translateX(200px);
  }
  20% {
    opacity: 1;
    -webkit-transform: translateX(160px);
            transform: translateX(160px);
  }
  80% {
    opacity: 1;
    -webkit-transform: translateX(-160px);
            transform: translateX(-160px);
  }
  to {
    opacity: 0;
    -webkit-transform: translateX(-200px);
            transform: translateX(-200px);
  }
}

@keyframes moving_right_to_left {
  from {
    opacity: 0;
    -webkit-transform: translateX(200px);
            transform: translateX(200px);
  }
  20% {
    opacity: 1;
    -webkit-transform: translateX(160px);
            transform: translateX(160px);
  }
  80% {
    opacity: 1;
    -webkit-transform: translateX(-160px);
            transform: translateX(-160px);
  }
  to {
    opacity: 0;
    -webkit-transform: translateX(-200px);
            transform: translateX(-200px);
  }
}

.hand-img {
  position: relative;
  top: -100px;
  -webkit-transform-origin: 50% 50% 0;
      -ms-transform-origin: 50% 50% 0;
          transform-origin: 50% 50% 0;
  -webkit-animation: 2s linear 0s infinite normal both running moving_right_to_left;
          animation: 2s linear 0s infinite normal both running moving_right_to_left;
}

.next-img {
  position: relative;
  top: -200px;
  left: -20px;
}

.static-bottom {
  position: relative;
  top: -20px;
}

@media (min-width: 768px) {
  .hand-img {
    -webkit-animation: none;
            animation: none;
  }
}


