.indicator {
  position: fixed;
  z-index: 999999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: none;
  /* background-color: rgba(0, 0, 0, 1); */
}

.indicator.show {
  display: block;
}

.indicator-text {
  background-color: darkblue;
  color: whitesmoke;
  text-shadow: 0 0 5px white;
  padding: 0.5em;
  border-radius: 0.255rem;
}

.for-small.indicator.show {
  display: block;
}

.for-large.indicator.show {
  display: none;
}

@media (min-width: 768px) {
  .for-small.indicator.show {
    display: none;
  }
  .for-large.indicator.show {
    display: block;
  }
}


